<script>
    import VueCookies from "vue-cookies";
    import {Message} from "element-ui";

    export default {
        name: "ItemImport",
        data() {
            return {
                actionUrl: "",
                uploadSuccess: false,
                headers: {
                    "Authorization": ""
                },
                excelExample: require('../../assets/excel.jpg')
            }
        },
        created() {
            this.headers.Authorization = VueCookies.get("token");
            this.actionUrl = `${process.env.VUE_APP_API_URL}/api/v1/projects/${this.$route.params.id}/upload`;
        },
        methods: {
            beforeExcelUpload(file) {
                console.log(file.type);
                const isExcel = file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' || 'text/csv';

                if (!isExcel) {
                    Message.error(' Excel  Csv !');
                }

                return isExcel
            },
            onSuccess(response, file, fileList) {
                //this.uploading = false;
                //setTimeout(()=>this.fetchStream(),1000)
                this.uploadSuccess = true
            },
        }
    }
</script>

