<template>
    <div class="wang-editor">
        <div ref="editorElem"
             style="text-align:left;"
             @paste="pasteHandle"></div>
    </div>
</template>

<script>
    // 
    import E from 'wangeditor'
    import axios from "axios";
    import VueCookies from "vue-cookies";

    export default {
        name: "WangEditor",
        data() {
            return {
                editor: null,
                content: "",
            }
        },
        watch: {
            value: {
                immediate: true,
                handler: function (val) {
                    this.content = val;
                }
            },
            content: function (val) {
                this.$emit('input', val)
            }
        },
        props: {
            value: {
                type: [String]
            }
        },
        mounted() {
            this.editor = new E(this.$refs['editorElem']);
            this.editor.customConfig.onchange = html => {
                this.content = html;
            };

            this.editor.customConfig.menus = [
                // 
                'head', // 
                'bold', // 
                'fontSize', // 
                'italic', // 
                'underline', // 
                'strikeThrough', // 
                'link', // 
                'list', // 
                'justify', // 
                'quote', // 
                'image', // 
                'table', // 
                'code', // 
            ];
            this.editor.customConfig.uploadImgServer = `${process.env.VUE_APP_API_URL}/api/v1/upload`;
            this.editor.customConfig.uploadImgMaxSize = 3 * 1024 * 1024
            this.editor.customConfig.uploadFileName = 'file'
            this.editor.customConfig.uploadImgHeaders = {
                "Authorization": "Bearer " + VueCookies.get("token"),
            }
            this.editor.customConfig.uploadImgHooks = {
                customInsert: function (insertImg, result, editor) {
                    // 
                    // insertImg editor result 

                    //  {url:'....'} 
                    let url = result.data.host + result.data.filename
                    insertImg(url)

                    // result  JSON 
                }
            }
            this.editor.create(); // 
            this.editor.txt.html(this.content)
        },
        methods: {
            pasteHandle(e) {
                if (!(e.clipboardData && e.clipboardData.items)) {
                    return false;
                }
                for (let i = 0, len = e.clipboardData.items.length; i < len; i++) {
                    let item = e.clipboardData.items[i];
                    //console.log(item)
                    if (item.kind === "file") {
                        let blob = item.getAsFile();
                        //row.imageUrl = URL.createObjectURL(blob)

                        //console.log(row)
                        //row.file_path = 0
                        let params = new FormData();
                        params.append('file', blob, 'image.png')
                        axios.post(`${process.env.VUE_APP_API_URL}/api/v1/upload`, params, {
                            headers: {
                                "Content-Type": "multipart/form-data"
                            }
                        }).then(({data}) => {
                            //this.editor.cmd.do('insertHTML', `<img src="${data.data.host + data.data.filename}">`)
                        })
                    }
                }

            },
        }
    }
</script>

<style lang="scss" scoped>
    .wang-editor {
        .w-e-text-container {
            height: 150px !important;
        }
    }
</style>